@import url("https://fonts.googleapis.com/css?family=Montserrat&display=swap");
@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.4.1/font/bootstrap-icons.css");

html,
body {
  height: 100%;
  font-family: "Montserrat", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: #979797;
  font-size: 35px;
  font-weight: 600;
}

p {
  font-size: 18px;
  color: #979797;
  line-height: 1.8;
}

img {
  max-width: 100%;
}

input:focus,
select:focus,
button:focus,
textarea:focus {
  outline: none;
}

a,
a:active,
a:focus {
  text-decoration: none !important;
  outline: none;
}

ul,
ol {
  padding: 0;
  margin: 0;
}

ol,
ul,
dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

.text-center {
  text-align: center;
}

.set-bg {
  background-repeat: no-repeat;
  background-size: cover;
}

.mb-30 {
  margin-bottom: 30px;
}

button.btn-back,
button.btn-switch-lang {
  cursor: pointer;
}

textarea {
  resize: none;
}

.alert {
  position: relative;
  padding: 0.75rem 1.25rem;
  margin-bottom: 1rem;
  border: 1px solid transparent;
  border-radius: 0.25rem;
}

.alert.alert-success {
  color: #155724;
  background-color: #d4edda;
  border-color: #c3e6cb;
}

.alert.alert-success .alert-link {
  color: #0b2e13;
}

.alert.alert-danger {
  color: #721c24;
  background-color: #f8d7da;
  border-color: #f5c6cb;
}

.alert.alert-danger .alert-link {
  color: #491217;
}

/* ------------------------ Header Section -------------------------- */
.header-section {
  padding-top: 0px;
  position: relative;
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.3);
}

.header-section.sticky {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: #fff;
  z-index: 2;
}

.header-section .site-logo {
  width: 130px;
  height: 100%;
  margin: 5px 0 0 0;
  text-align: center;
}

.header-section .site-logo img {
  width: 200px;
  height: 92px;
}

/* MAIN MENU */
.main-menu ul {
  list-style: none;
  height: 100%;
  display: flex;
  margin: 0;
}

.main-menu ul li {
  display: inline-flex;
  align-items: center;
}

.main-menu ul li a {
  display: inline-flex;
  align-items: center;
  font-size: 18px;
  line-height: 1.5;
  color: #001418;
  padding: 0 15px;
  border-bottom: 1px solid transparent;
}

.main-menu ul li a span {
  display: block;
}

.main-menu ul li a.active,
.main-menu ul li a:hover {
  border-bottom: 1px solid #001418;
}

.nav-switch {
  display: none;
}

.container {
  margin-top: 100px;
}

.container.homeLarge {
  position: relative;
  background-color: black;
  height: 100vh;
  min-height: 25rem;
  width: 100%;
  max-width: 100%;
  overflow: hidden;
  margin: 0;
}

.container.homeLarge .videoPlayer {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: 0;
  transform: translateX(-50%) translateY(-50%);
}

.container.homeLarge h4 {
  text-align: center;
}

/* Section Title */
h1.section-title {
  color: #979797;
  margin: 0 0 20px 0;
}

h1.section-title span {
  display: block;
  text-align: center;
  color: #979797;
}

/* ... (CONTENT TRUNCATED FOR BREVITY) ... */

@media only screen and (min-width: 576px) {
  .container.about-us .description img {
    float: right;
    padding-left: 15px;
  }
}

@media only screen and (min-width: 992px) {
  .site-logo {
    width: 250px;
  }
}

@media only screen and (min-width: 768px) {
  .header-section .site-logo {
    width: 150px;
  }
  .container {
    margin-top: 130px;
  }
  .about-us .description img {
    padding-bottom: 0;
    padding-right: 0;
  }
}

@media only screen and (max-width: 767px) {
  .main-menu {
    position: absolute;
    background: #f8f8f8;
    width: 100%;
    z-index: 999;
    left: 0;
    top: 71px;
    padding: 0;
    display: none;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.3);
  }
  .main-menu ul {
    display: block;
  }
  .main-menu ul li {
    display: block;
    padding: 15px 0;
    border-bottom: 1px solid #ddd;
  }
  .main-menu ul li:last-child {
    border-bottom: none;
  }
  .main-menu ul li a {
    display: inline-flex;
    align-items: center;
  }
  .main-menu ul li a.active {
    border-bottom: none;
    font-weight: bold;
  }
  .main-menu ul li:last-child a.active {
    border-bottom-color: #001418;
    border-bottom-style: solid;
    border-bottom-width: 1px;
  }
  .nav-switch {
    position: absolute;
    left: 25px;
    font-size: 30px;
    color: #333;
    top: 23px;
    display: block;
  }
  .header-section .site-logo img {
    width: 130px;
    height: 60px;
  }
  .header-btn {
    display: none;
  }
  .container.home {
    margin-top: 70px;
    max-width: 100%;
    padding: 0;
  }
  .container.home .media {
    display: flex;
    align-items: center;
    overflow: visible;
    max-width: 100%;
  }
  .container.home .media .videoPlayer {
    height: 100%;
    width: 100%;
  }
  .footer-section {
    margin-top: 0;
  }
  .footer-section .container {
    margin-top: 0;
  }
  .contact .contact-form .btn-submit {
    width: 100%;
  }
}
